.gridContainer{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    padding: 2vh 1.5vw;
    background-color: rgb(250, 250, 250);
}
.webp{
    animation-play-state: paused;
}
.authorlogo img{
    width: 35px;
    height: 35px;
    border-radius: 50%;
    margin-right:  0.52vw;
}
.a{
    margin: 1.5vh 0;
    display: flex;
    flex-direction: row;
}
.desc{
    font-size: 14px;
    font-weight: 600;
    font-family: 'Roboto', sans-serif;
}
.author{
    font-size: 13.88px;
    color: gray;
}
.views{
    font-size: 13.88px;
    color: gray;
}