.suggest{
    display: flex;
    flex-direction: row;
    align-items: center;
    border-bottom: 1px solid rgb(233, 222, 222);
    height: 60px;
    padding: 0 1.02vw;
    padding-top: 54px;
}
.s{

    height: 55%;
    margin: 0 9px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(243, 243, 243);
    border-radius: 15px;
    border: 1px solid rgb(206, 206, 206);
    
}
.s a{
    text-decoration: none;
    color: black;
}
.s p{
    font-size: 15px;
    padding: 0 13px;

}