.body{
    height: fit-content;
    padding: 7vh 2.85vw;
    background-color: rgb(250, 250, 250);
}
.body p{
    font-size: 16px;
    font-family: 'Ubuntu', sans-serif;

}
.gridcontent{
    display: grid;
    grid-template-rows: repeat(1,1fr);
}
.explore{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 0.5vw;
    align-items: center;
}
.temp{
    display: flex;
    flex-direction: row;
}
.explore img{
    width: 245px;
    height: 145px;
    margin-bottom: 1.7vh;
    margin-right: 1.2vw;
}
.description p{
    margin: 0 0;
    width: 60%;
    font-family: 'Ubuntu', sans-serif;
    font-size: 17.5px;
}
.desc1 p{
    font-size: 12px;
    font-weight: 200;
    width: 60%;
    font-family: 'Ubuntu', sans-serif;
    color: gray;
}
.righta1{
    height: 148px;
}