body{
    scroll-behavior: smooth;
}
.items img{
    height: 24px;
    width: 24px;
}
.bar{
    position: fixed;
    width:14%;
    padding-top: 58px;
}
.items{
    color: black;
    text-decoration: none;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 1.5vw;
    margin-right: 0.172vw;
    height: 40.82px;
    /* border: 1px solid gray; */
}
.items a{
    display: flex;
    flex-direction: row;
    align-items: center;
    text-decoration: none;
    color: black;
    text-decoration: none;
}
.items:hover{
    background-color: rgb(243, 243, 243);
}
.items p{
    font-size: 14.15px;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    margin-left: 1.2vw;
}
.item1{
    margin-top: 1.25vh;
    margin-right: 0.25vw;
    border-bottom: 1px solid rgb(206, 206, 206);
    padding-bottom: 2vh;
    text-decoration: none;
}
.item2{
    margin-right: 0.25vw;
    border-bottom: 1px solid rgb(206, 206, 206);
    padding-bottom: 2vh;
    padding-top: 1.35vh;
}
.item1 a{
    text-decoration: none;
    
}
.item2 a{
    text-decoration: none;
    
}


.item3{
    display: flex;
    flex-direction: column;
    margin-right: 0.25vw;
    border-bottom: 1px solid rgb(206, 206, 206);
    padding-bottom: 2vh;
    margin-left: 1.3vw;
    font-size: 15px;
}

.signin{
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border: 1px solid #24a0ed;
    height: 40px;
    width: 105px;
   margin-right: 20px;
   border-radius: 3px;
  }
  .signin img{
    height: 24px;
    width: 24px;
   margin-right: 8px;
  }
  .signin h5{
    color: #24a0ed;
    
  }