.container{
  display:flex;
  flex-direction: row;
  align-items: center;
}
.navbar{
  display: flex;
  flex-direction: row;
  height: 59px;
  position: fixed;
  background-color: white;
  width: 100%;
}
.logo img{
width:89px;
height: 51px;
cursor: pointer;
}
.menu img{
  margin: 0 25px;
  width: 25px;
  height: 26px;
  cursor: pointer;
}
.left{
  display:flex;
  flex-direction: row;
  align-items: center;
  width: 14%;

  /* border: 1px solid gray; */

}
.middle{
  display:flex;
  flex-direction: row;
  align-items: center;
  justify-content: right;
  width: 56%;


  /* border: 1px solid gray; */


  border-bottom: 1px solid rgb(233, 222, 222);
}
.searchbar{
  display: flex;
  flex-direction: row;
  border: 1px solid rgb(206, 206, 206);
  height: 39px;
  width: 67.85%;
  margin: 0 13px;
  border-radius: 1px;
}
.searchico img{
  width: 24px;
  height: 24px;
}
.searchico{
  cursor: pointer;
  background-color: rgb(243, 243, 243);
  width: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-left: 1px solid rgb(206, 206, 206);

}
.searchbar input{
  outline: none;
  border: none;
  width: 90%;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  padding-left: 11px;
}
.searchbar input::placeholder{
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
}
.voice img{
  width: 23px;
  height: 23px;
  cursor: pointer;
}
.voice{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(243, 243, 243);
  border-radius: 50%;
  height: 35px;
  width: 37px;
}
.right{
  width: 30%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: right;
  border-bottom: 1px solid rgb(233, 222, 222);

  /* border: 1px solid gray; */



}
.buttons{
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
}
.buttons img{
  height: 26px;
  width: 26px;
  margin: 0 12.5px;
}
.signin{
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border: 1px solid #24a0ed;
  height: 66%;
  width: 105px;
 margin-right: 20px;
 border-radius: 3px;
}
.signin img{
  height: 26px;
  width: 26px;
 margin-right: 8px;
}
.signin h5{
  color: #24a0ed;
  
}







